* {
  box-sizing: border-box;
}
body {
  height: 99%;
}
body {
  padding: 4vh 10vw;
  font-family: 'Inter', sans-serif;
  display: flex;
  justify-content: center;
}
.canvas {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 95vw;
  max-width: 950px;
}

nav {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.875rem;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li {
  cursor: pointer;
  user-select: none;
  padding-left: 40px;

  float: left;
}
li:hover {
  color: hsl(5, 85%, 63%);
}
nav > img {
  width: 3rem;
  height: auto;
  object-fit: contain;
}
.web-3 {
  margin-bottom: 0.75rem;
}
.main-text {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}
.main-desc {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 50%;
  height: 100%;
}
.block2 {
  padding: 15px 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 40%;
  background-color: hsl(240, 100%, 5%);
  height: 100%;
}

.section1 {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  height: 60vh;
}

.font1 {
  line-height: 2.5rem;
  display: flex;
  justify-content: center;
  width: 50%;
  height: 100%;
  font-weight: 800;
  font-size: 2.5rem;
}
.main-info {
  height: fit-content;
  margin: auto;

  width: 100%;
}
.font3 {
  color: hsl(236, 13%, 42%);
  font-size: 0.813rem;
  font-weight: 400;
}

.block1 {
  display: flex;
  flex-direction: column;
  margin-right: 1.875rem;
  width: 65%;
}

p {
  margin: 0;
}
.font2 {
  font-size: 1rem;
  color: white;
  font-weight: 700;
}
.font4 {
  margin-bottom: 20px;
  font-weight: 700;
  color: hsl(35, 77%, 62%);
  font-size: 2.188rem;
}
.btn {
  cursor: pointer;
  user-select: none;
  width: 150px;
  text-align: center;
  padding: 12px 7px;
  letter-spacing: 0.2rem;
  color: white;
  background-color: hsl(240, 100%, 5%);
  font-size: 0.75rem;
  font-weight: 400;
}

.btn:hover {
  background-color: hsl(5, 85%, 63%);
}

.block2 > .font3 {
  color: hsl(233, 8%, 79%);
  margin-top: 15px;
}
.block2 > .font2:hover {
  cursor: pointer;
  color: hsl(35, 77%, 62%);
}
.line {
  margin-top: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid hsl(236, 13%, 42%);
}
.section2 {
  margin-top: 40px;
  flex-grow: 1;
  max-height: 15vh;
}
.section2 > ul {
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
}
.font5 {
  font-size: 1.563rem;
  font-weight: 700;
  color: hsl(233, 8%, 79%);
  margin-bottom: 5px;
}
.section2 > ul > li {
  width: calc(100% / 3);
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.desc {
  margin-left: 10px;
}
.section2-img {
  height: 100%;
}

.font6 {
  font-size: 0.938rem;
  color: hsl(240, 100%, 5%);
  font-weight: 800;
  margin-bottom: 10px;
}
.font6:hover {
  color: hsl(5, 85%, 63%);
}
.mobile {
  display: none;
}
@media (max-width: 1200px) {
  html {
    font-size: 14px;
  }
}
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Aquí puedes ajustar el nivel de transparencia cambiando el valor del último parámetro (0.5 en este caso) */
  z-index: 1; /* Asegura que el elemento esté por encima de otros elementos */
}
@media (max-width: 800px) {
  nav{

    padding-top: .5rem ;
    width: 100% ;
  }
  .close {
    top: calc(4vh + 6px);
    position: absolute;
    right: 18px;
  }
  .nav-links {
    display: none;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 3;
    background-color: white;
    height: 100vh;
    width: 55%;
    max-width: 300px;
  }

  .show {
    display: block;
  }
  .nav-links.show {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .nav-links > li {
    padding-left: 2rem;
    margin-bottom: 1.5rem;

    font-weight: 400;
    font-size: 1.5rem;
    color: hsl(240, 100%, 5%);
  }
  p {
    width: 100%;
  }
  .web-3 {
    width: 100%;
  }
  .main-text {
    flex-direction: column;
  }
  .block1 {
    width: 100%;
    justify-content: center;
    margin: 0;
    align-items: center;
    margin-bottom: 2rem;
  }
  .block2 {
    width: 100%;
  }
  .section1 {
    flex-direction: column;
    height: auto;
  }
  .mobile {
    display: block;
  }
  .desktop {
    display: none;
  }
  .main-text {
    margin-top: 1rem;
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  .main-info {
    text-align: left;
  }
  .main-text > p {
    width: 100%;
  }
  .main-desc {
    width: 100%;
  }
  .main-desc > p {
    text-align: left;
    margin: 1rem 0;
  }
  body {
    padding: 0;
  }
  .section2 {
    max-height: none;
  }
  .section2 > ul {
    flex-direction: column;
  }
  .section2-img {
    height: 100%;
  }
  .section2 > ul > li {
    width: 100%;
    height: 100px;
    margin-bottom: 1.2rem;
  }
}
